body {
    display: flex;
    justify-content: center;
    height: 100vh;
    background-color: #f55d5e;
    font-size: 20px;
    font-family: 'Cormorant Infant', sans-serif;
    color: #ffffff;
}

#root {
    display: flex;
    flex-direction: column;

    align-items: center;
    width: 70%;
}

.quiz{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
    width: 80%;
    flex: 1 0 auto;
}

.answers{
    color: #ffffff;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.question{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}
.question.visible{
    visibility: hidden;
}
.question h3{
    flex-basis: 100%;
    text-align: center;
    margin-bottom: 10px;
}
.answer {
    width: 60%;
    background-color: #fff;
    padding: 5px 15px;
    border: none;
    font-family: 'Cormorant Infant', sans-serif;
    border-radius: 50px;
    color: #b6b6b8;
    font-weight: 500;
    font-size: 12pt;
    margin-top: 10px;
    box-shadow: 0px 4px 1px 1px rgba(0, 0, 0, 0.15);
    -webkit-transition-duration: .3s;
            transition-duration: .3s;
}
.answer:hover{

}

.answer.good{
    background: green;
    color: #ffffff;
}
.answer.error{
    background: red;
    color: #ffffff;
}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    overflow: hidden;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

.item-1 {
    width: 20px;
    height: 20px;
    background: #f583a1;
    border-radius: 50%;
    background-color: #eed968;
    margin: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
}
@-webkit-keyframes scale {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%,
    75% {
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
    }
    78%, 100% {
        opacity: 0;
    }
}
@keyframes scale {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%,
    75% {
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
    }
    78%, 100% {
        opacity: 0;
    }
}
.item-1:before {
    content: '';
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #eed968;
    opacity: 0.7;
    -webkit-animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
            animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
    -webkit-transform: scale(1);
            transform: scale(1);
}

