.level{
    width: 80%;
}
ul{
    display: flex;
    margin: 20px 0;
    justify-content: space-between;
    flex-wrap: wrap;
}
.level li{
    text-decoration: none;
    outline: none;
    display: inline-block;
    width: 140px;
    height: 45px;
    line-height: 45px;
    border-radius: 45px;
    margin: 10px 20px;
    font-family: 'Cormorant Infant', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    font-weight: 600;
    color: #524f4e;
    background: white;
    box-shadow: 0 8px 15px rgba(0,0,0,.1);
    cursor: pointer;
    -webkit-transition: .3s;
    transition: .3s;
}
.level li:hover{
    background: #eeb74c;
    box-shadow: 0 15px 20px rgba(72,22,73,.4);
    color: white;
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
}

@media screen and (max-width: 1285px) {
    .level{
        width: 60%;
    }
}

@media screen and (max-width: 857px) {
    .level{
        width: 80%;
    }
}

@media screen and (max-width: 643px) {
    ul{
        justify-content: center;
    }
}
.rightanswers{
    width: 100%;
    flex: 0 0 auto;
    font-size: 18px;
}
.right-answer{
    display: flex;
    justify-content: space-between;
}
.right-answer span:first-child{
    width: 70%;
}
.right-answer span:last-child{
    text-decoration: underline;
    width: 30%;
    text-align: right;
}
.wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 50%;
}

